*,
*::before,
*::after {
  box-sizing: border-box;
}
html {
  min-width: 270px;
}
body {
  margin: 0;
  font-family: 'Work Sans', 'Arial', sans-serif;
  font-weight: 400;
}
img {
  max-width: 100%;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
